'use client';

import { Container, Title, Text, Table, Badge, Card, Group } from '@mantine/core';

export default function Schedule() {
    const events = [
        { date: '2025-12-10', time: '10:00 AM', event: 'Annual General Meeting', location: 'Auditorium', type: 'Official' },
        { date: '2025-12-12', time: '02:00 PM', event: 'Tech Seminar', location: 'Medium Conference Room', type: 'Training' },
        { date: '2025-12-15', time: '09:00 AM', event: 'New Employee Orientation', location: 'Video Conference Room', type: 'HR' },
        { date: '2025-12-20', time: '06:00 PM', event: 'Year-End Gala', location: 'Auditorium', type: 'Social' },
    ];

    const rows = events.map((event, index) => (
        <Table.Tr key={index}>
            <Table.Td>{event.date}</Table.Td>
            <Table.Td>{event.time}</Table.Td>
            <Table.Td style={{ fontWeight: 500 }}>{event.event}</Table.Td>
            <Table.Td>{event.location}</Table.Td>
            <Table.Td>
                <Badge color={event.type === 'Official' ? 'blue' : event.type === 'Social' ? 'pink' : 'green'} variant="light">
                    {event.type}
                </Badge>
            </Table.Td>
        </Table.Tr>
    ));

    return (
        <Container size="lg" py="xl">
            <Title order={1} mb="xl">Event Schedule</Title>
            <Card padding="lg" radius="md" withBorder>
                <Table verticalSpacing="sm" highlightOnHover>
                    <Table.Thead>
                        <Table.Tr>
                            <Table.Th>Date</Table.Th>
                            <Table.Th>Time</Table.Th>
                            <Table.Th>Event Name</Table.Th>
                            <Table.Th>Location</Table.Th>
                            <Table.Th>Type</Table.Th>
                        </Table.Tr>
                    </Table.Thead>
                    <Table.Tbody>{rows}</Table.Tbody>
                </Table>
            </Card>
        </Container>
    );
}
